import React from "react";
import '../styles/Contact.css';
import PizzaLeft from '../assets/pizzaLeft.jpg'

function Contact() {
    return (
        <div className="contact">
            <div className="leftSide" style={{backgroundImage:`url(${PizzaLeft})`}}></div>
            <div className="rightSide">
                <h1>Contact us</h1>
                <form className="contactForm" method="POST">
                    <label htmlFor="name">full name</label>
                    <input name="name" placeholder="enter full name.." type='text'></input>

                    <label htmlFor="email">e-mail</label>
                    <input name="email" placeholder="enter email.." type='email'></input>

                    <label htmlFor="message">message</label>
                    <textarea name="message" placeholder="what do you want to tell us.." type='textarea' rows='6' cols='20'></textarea>

                    <button type="submit">send</button>
                </form>
            </div>
        </div>
    )
}

export default Contact;